import React from 'react';
import { Link } from 'react-router-dom';

const AboutSection = (props) => {
  return (
    <section id="about" className="section about">
      <div className="wrapper wrapper--center">
        <h2 className="section__title">Qui suis-je ?</h2>
        <hr />
        <p>
          Je suis un <strong>développeur web</strong> autodidacte et passionné qui se spécialise dans le développement <strong>Front-End</strong>. Mon intérêt pour la programmation remonte à mes 16 ans quand j'ai entré avec émerveillement mes premières lignes de Ti-Basic dans ma <a href="https://fr.wikipedia.org/wiki/TI-83" target="_blank" rel="noopener noreferrer">Ti 83+</a>.
        </p>
        <p>
          J'envisage actuellement de débuter une formation en alternance en tant que <a href="https://www.ifocop.fr/formations-metiers/web-digital/formation-developpeur-full-stack-js/" target="_blank" rel="noopener noreferrer">développeur Full Stack JS</a> à l'<strong>Ifocop</strong> dans le XIe arrondissement de Paris.
        </p>
        <p>
          Dans le cadre de cette formation je suis à la recherche d'une entreprise pour m'accueillir en <strong>contrat pro</strong> dès maintenant en vue d'une rentrée le <strong>16&nbsp;mars&nbsp;2018</strong>. Si vous êtes intéressé, vous pouvez consulter mon CV en <a href="/CV Thomas Vandenhede.pdf" target="blank">cliquant ici</a>, et me contacter en suivant <Link to='/contact'>ce lien</Link>.
        </p>
        <p>
          La page sur laquelle vous vous trouvez est une SPA (Single Page Application) créée avec ❤ par moi-même en <strong>React.js</strong> et <strong>Sass</strong>.
        </p>
        <a href="/CV Thomas Vandenhede.pdf" target="blank" className="hero__cta btn btn--rounded btn--shadow">voir mon CV</a>
      </div>
    </section>
  );
};

export default AboutSection;


